form {
  display: flex;
  flex-direction: column;
  width: 30vw;
  justify-content: space-evenly;
  align-items: stretch;
  height: 60vh;
  background-color: white;
  border-radius: 10px;
  background-color: white;
  border-top-right-radius: 50px;
}
.loginSection {
  display: flex;
  justify-content: flex-end;
  background-image: url("https://images.pexels.com/photos/16379061/pexels-photo-16379061/free-photo-of-m-ong-thanh-luxury-nha-trang-hotel.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-size: cover;
  height: 100vh;
  align-items: center;
  padding-right: 50px;
}

.heading {
  font-size: 30px;
  color: Black;
  align-self: center;
}

.loginButton {
  background-color: blue;
  color: white;
  width: 80px;
  border: 0px;
  border-radius: 5px;
  height: 30px;
  align-self: center;
}
.label {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: 20px;
}
label {
  font-weight: bold;
  font-style: "Roboto";
  padding-bottom: 10px;
}

input {
  width: 20vw;
}
